<template>
  <div class="input-container w-max">
    <div class="input-items mt-32">
      <h2>ログイン</h2>
      <div>本サイトではデータベースにsupabaseを使用しています。</div>
      <div>
        利用するためには会員登録が必要なため、「登録する」のチェックボックスにチェックを入れ、メールアドレスとパスワードを登録してください。
      </div>
      <common-check-box title="登録する" type="checkbox" v-model="isSignUp" />
      <common-input title="メールアドレス" type="text" v-model="accountName" />
      <common-input title="パスワード" type="password" v-model="accountAsset" />
      <common-button
        class="w-max mb-20 mt-20"
        @click="loginAccount"
        v-show="!isSignUp"
        >ログイン
      </common-button>
      <common-button
        class="w-max mb-20 mt-20"
        @click="submitAccount"
        v-show="isSignUp"
        >登録</common-button
      >
    </div>
    <div>{{ error }}</div>
    <div v-if="message">{{ message }}</div>
  </div>
</template>

<script lang="ts">
import CommonInput from "@/components/Common/CommonInput.vue";
import CommonButton from "@/components/Common/CommonButton.vue";
import CommonCheckBox from "@/components/Common/CommonCheckBox.vue";
import { ref } from "vue";
import { supabase } from "@/services/supabase";
import { defineComponent } from 'vue';

export default defineComponent({
  name: "LoginPage",
  components: {
    CommonInput,
    CommonButton,
    CommonCheckBox,
  },
  setup(props, { emit }) {
    const accountName = ref<string>("");
    const accountAsset = ref<string>("");
    const error = ref<string | null>(null);
    const message = ref<string | null>(null);
    const isSignUp = ref<boolean>(false);

    const close = () => {
      emit("close");
    };

    const submitAccount = async () => {
      error.value = null;
      message.value = null;

      // TBLへの登録処理
      const { error: signUpError } = await supabase.auth.signUp({
        email: accountName.value,
        password: accountAsset.value,
      });

      if (signUpError) {
        // error.value = signUpError.message;
        error.value =
          "ユーザーの登録に失敗しました。時間をおいて再度お試しください。";
      } else {
        message.value =
          "ユーザーが正常に登録されました。確認メールをチェックしてください。";
      }
    };

    const loginAccount = async () => {
      error.value = null;
      message.value = null;

      const { data, error: signInError } =
        await supabase.auth.signInWithPassword({
          email: accountName.value,
          password: accountAsset.value,
        });

      if (signInError) {
        if (signInError.message.includes("confirmation")) {
          error.value =
            "アカウントの確認が必要です。確認メールをチェックしてください。";
        } else {
          //   error.value = signInError.message;
          error.value = "ログイン失敗。アカウント情報を確認してください";
        }
      } else {
        console.log("suucess");
        message.value = "サインイン成功!";
        // サインイン成功後の処理をここに追加
      }
    };

    return {
      accountName,
      accountAsset,
      isSignUp,
      error,
      message,
      submitAccount,
      close,
      loginAccount,
    };
  },
});
</script>

<style scoped>
.input-container {
  max-width: 70%;
  margin: 0 auto;
}
</style>
